//
// YouTube & Vimeo Wrapper
// --------------------------------------------------


.videoWrapper {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;

	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.entry-content & {

		@include responsive(sm) {
			padding: 0;
			height: auto;

			iframe {
				position: relative;
				width: auto;
				height: auto;
			}
		}

	}
}